:root {
  --color-accent: #060b35;
  --color-secondary: #616070;
  --color-blue-accent: #00acdc;
  --color-light-border: #f3f3f3;
}

body {
  margin: 0 auto;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
}

.product-card {
  box-sizing: border-box;
  border: 1px solid var(--color-light-border);
  border-radius: 5px;
  width: 200px;
  height: 406px;
}

.product-card__content {
  padding: 32px 16px 0;
}

.product-card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-card__description {
  color: var(--color-accent);
  line-height: 18px;
}

.product-card__title {
  margin: 0 0 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__rate {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.product-card__stars {
  align-items: center;
  display: flex;
}

.product-card__stars .product-card__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.product-card__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.product-card__code {
  color: var(--color-secondary);
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__text {
  font-size: 10px;
  line-height: 14px;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.product-card__label {
  color: var(--color-secondary);
  font-size: 12px;
  line-height: 18px;
}

.product-card__cost {
  color: var(--color-accent);
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.button {
  background-color: var(--color-blue-accent);
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin: 16px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: block;
}

.button:hover {
  color: var(--color-blue-accent);
  border: 1px solid var(--color-blue-accent);
  background-color: #fff;
}

